import { ComponentProps, ReactNode, useMemo } from 'react';
import {
  FieldErrors,
  Path,
  useFormContext,
  UseFormReturn,
} from 'react-hook-form';
import get from 'lodash/get';
import has from 'lodash/has';

// Components
import Field from '../Field';

// Types
import { TestMetadata } from 'types';

// Styles
import './FormField.scss';

export interface ControlProps {
  /** Class name for component */
  className?: string;
  /** Boolean to display error state of component */
  isError?: boolean;
}

export interface FormFieldProps<I extends Record<string, unknown>> {
  fieldName: Path<I>;
  /** Text of visible input label */
  labelText?: ReactNode;
  /** Tooltip icon and hover text */
  tooltip?: string[];
  /** Boolean to determine if control is required */
  required?: boolean;
  /** Helper text, if any */
  helper?: ReactNode;
  /** Actual control component to be displayed */
  control: (formMethods: UseFormReturn<I>, props: ControlProps) => ReactNode;
  /** className for top level component */
  className?: string;
  /** Control success message, if any */
  success?: ReactNode;
  /** Passing props to the label */
  labelProps?: ComponentProps<'label'> & TestMetadata;
  /* Label for override */
  labelFor?: string;
  /** Fix the feedback section height so it doesn't jump as error or success feedback is displayed */
  fixedHeightFeedback?: boolean;
  /* To override the required text */
  requiredText?: string;
  /* Custom Validation for error message  */
  customGetErrorMessage?: (errors: FieldErrors<I>, values: I) => ReactNode;
  /* Custom Validation for isError  */
  customIsErrorValidation?: (errors: FieldErrors<I>, values: I) => boolean;
  readOnly?: boolean;
  truncate?: 'truncate' | 'wrap';
}

const FormField = <I extends Record<string, unknown>>({
  fieldName,
  labelText,
  tooltip,
  required,
  helper,
  control,
  className,
  success,
  labelProps,
  labelFor,
  customGetErrorMessage,
  customIsErrorValidation,
  readOnly,
  ...otherProps
}: FormFieldProps<I>): JSX.Element => {
  const { ...methods } = useFormContext<I>();
  const error = useMemo(() => {
    if (readOnly) {
      return;
    }

    return customGetErrorMessage
      ? customGetErrorMessage(methods.formState.errors, methods.getValues())
      : get(methods.formState.errors, `${String(fieldName)}.message`);
  }, [readOnly, customGetErrorMessage, fieldName, methods]);

  return (
    <Field
      labelText={labelText}
      labelFor={labelFor || fieldName.toString()}
      tooltip={tooltip}
      required={required}
      helper={!readOnly ? helper : ''}
      error={error}
      success={methods.formState.isSubmitSuccessful && success}
      className={className}
      labelProps={labelProps}
      control={control(methods, {
        isError: customIsErrorValidation
          ? customIsErrorValidation(
              methods.formState.errors,
              methods.getValues(),
            )
          : has(methods.formState.errors, fieldName),
      })}
      {...otherProps}
    />
  );
};

export default FormField;
