import { FC, ReactElement } from 'react';
import classNames from 'classnames';

// Components
import Tooltip from '../Tooltip';
import Icon from '../Icon';

// Types
import { SizeOptions, TestMetadata } from 'types';

// Styles
import './TooltipTemplate.scss';

export interface TooltipTemplateProps {
  /** tooltip content - can be an array of string or a custom React element */
  tooltipContent: ReactElement | string[];
  /** size options for the icon */
  size?: SizeOptions;
  /** classname */
  className?: string;
  // Test IDs for component sections
  testMetadata?: {
    icon?: TestMetadata;
    tooltip?: TestMetadata;
  };
}

const TooltipTemplate: FC<TooltipTemplateProps> = ({
  tooltipContent,
  size = 'smaller',
  className,
  testMetadata,
}) => (
  <Tooltip
    tippyProps={{ placement: 'right' }}
    tooltipContent={
      Array.isArray(tooltipContent) ? (
        <div>
          {tooltipContent.map((text) => (
            <div key={text}>{text}</div>
          ))}
        </div>
      ) : (
        tooltipContent
      )
    }
    className={classNames(className, 'sb-cp-tooltip-template')}
    testMetadata={testMetadata?.tooltip}
  >
    <Icon
      className="sb-cp-tooltip-template__icon"
      faPackage="fa-regular"
      iconName="fa-circle-info"
      iconType="fa"
      size={size}
      testMetadata={{
        iconFASpan: testMetadata?.icon,
      }}
    />
  </Tooltip>
);

export default TooltipTemplate;
