import { ComponentProps, FC, ReactNode } from 'react';
import classNames from 'classnames';

// Components
import TooltipTemplate from '../TooltipTemplate';

// Styles
import './Field.scss';

// Types
import { TestMetadata } from 'types';

export interface FieldProps {
  /** Text of visible input label */
  labelText?: ReactNode;
  /** Label for (same as related input name) */
  labelFor?: string;
  /** Tooltip icon and hover text */
  tooltip?: string[];
  /** Boolean to determine if field is required */
  required?: boolean;
  /** Helper text, if any */
  helper?: ReactNode;
  /** Field error message, if any */
  error?: ReactNode;
  /** Field success message, if any */
  success?: ReactNode;
  /** Actual control component to be displayed */
  control: ReactNode;
  /** className for top level component */
  className?: string;
  /** Passing props to the label */
  labelProps?: ComponentProps<'label'> & TestMetadata;
  /** Fix the feedback section height so it doesn't jump as error or success feedback is displayed */
  fixedHeightFeedback?: boolean;
  /** text to display for required fields */
  requiredText?: string;
}

const Field: FC<FieldProps> = ({
  labelText,
  labelFor,
  tooltip,
  required,
  helper,
  error,
  success,
  control,
  className = '',
  labelProps,
  fixedHeightFeedback = true,
  requiredText = '*Required',
  ...otherProps
}) => {
  const fieldClassName = classNames('sb-field', className);

  return (
    <div {...otherProps} className={fieldClassName}>
      {labelText && (
        <div className="sb-field__label">
          <label
            className="sb-field__label-text"
            htmlFor={labelFor}
            {...labelProps}
          >
            {labelText}
          </label>
          {tooltip && (
            <TooltipTemplate tooltipContent={tooltip} size="smaller" />
          )}
          {required && (
            <div className="sb-field__label-required-text">{requiredText}</div>
          )}
        </div>
      )}
      {control}
      {helper && <div className="sb-field__feedback">{helper}</div>}
      {error ? (
        <div className="sb-field__feedback sb-field__feedback--has-error">
          {error}
        </div>
      ) : success ? (
        <div className="sb-field__feedback sb-field__feedback--has-success">
          {success}
        </div>
      ) : (
        fixedHeightFeedback && <div className="sb-field__feedback">&nbsp;</div>
      )}
    </div>
  );
};

export default Field;
