import {
  ClassAttributes,
  DetailedHTMLProps,
  FC,
  TextareaHTMLAttributes,
  useContext,
  useEffect,
  useMemo,
  useState,
} from 'react';
import classNames from 'classnames';

// Components
import Typography from '../Typography';

// Styles
import './TextArea.scss';

// Types
import { TestMetadata } from 'types';

// Analytics
import {
  AnalyticsContext,
  handleAnalyticsEvent,
  TAnalyticsEventData,
} from '@dtci/dash/analytics';

export interface TextAreaProps
  extends DetailedHTMLProps<
    TextareaHTMLAttributes<HTMLTextAreaElement>,
    HTMLTextAreaElement
  > {
  /** string value of the text insides the text area */
  value?: string;
  /** Determine if field is required */
  required?: boolean;
  /** If label is not visible, use aria-label for screen readers */
  labelText?: string;
  /** ref form inner input **/
  inputRef?: ClassAttributes<HTMLTextAreaElement>['ref'];
  /** Width of the Text Area */
  width?: 'small' | 'medium' | 'large' | 'auto';
  /** Error State */
  isError?: boolean;
  /** default number of lines to determine height */
  rows?: number;
  /** Max length of characters required */
  maxLength?: number;
  /** show a character count */
  charCount?: boolean;
  // Test IDs for component sections
  testMetadata?: TestMetadata;
  /** Additional event information to pass to analytics tracking */
  analyticsEventData?: TAnalyticsEventData;
}

/** TextArea components are useful for editing large areas of text such as paragraphs */
const TextArea: FC<TextAreaProps> = ({
  className,
  inputRef,
  required,
  labelText,
  width = 'small',
  isError = false,
  rows,
  maxLength,
  charCount = false,
  testMetadata,
  analyticsEventData,
  ...otherProps
}) => {
  const textAreaClassNames = useMemo(
    () =>
      classNames(
        'sb-textarea',
        {
          'sb-textarea_error': isError,
          'sb-textarea__is-required': required,
          'sb-textarea__size-small': width === 'small',
          'sb-textarea__size-medium': width === 'medium',
          'sb-textarea__size-large': width === 'large',
          'sb-textarea__size-auto': width === 'auto',
          'sb-textarea__height-auto': rows,
        },
        className,
      ),
    [className, required, width, rows, isError],
  );

  const analyticsContext = useContext(AnalyticsContext);
  const [chars, setChars] = useState(0);
  const { onFocus, onBlur, onChange, defaultValue } = otherProps;

  useEffect(() => {
    if (defaultValue && charCount) {
      setChars(defaultValue?.toString().length);
    }
  }, [defaultValue, charCount]);

  const handleOnFocus = (e: React.FocusEvent<HTMLTextAreaElement>): void => {
    onFocus?.(e);
    handleAnalyticsEvent({
      analyticsContext,
      analyticsEventData,
      componentType: 'TextArea',
      actionName: 'focus',
      label:
        otherProps?.['aria-label'] ||
        otherProps?.name ||
        labelText ||
        testMetadata?.['data-testid'] ||
        className ||
        '',
      value: [{ text: e.target.value }],
    });
  };

  const handleOnBlur = (e: React.FocusEvent<HTMLTextAreaElement>): void => {
    onBlur?.(e);
    handleAnalyticsEvent({
      analyticsContext,
      analyticsEventData,
      componentType: 'TextArea',
      actionName: 'blur',
      label:
        otherProps?.['aria-label'] ||
        otherProps?.name ||
        labelText ||
        className ||
        testMetadata?.['data-testid'] ||
        '',
      value: [{ text: e.target.value }],
    });
  };

  const handleChange = (e: React.ChangeEvent<HTMLTextAreaElement>): void => {
    charCount && setChars(e.target.value.length);
    onChange?.(e);
  };

  return (
    <div>
      <textarea
        {...otherProps}
        onFocus={handleOnFocus}
        onBlur={handleOnBlur}
        className={textAreaClassNames}
        required={required}
        aria-required={required}
        aria-label={labelText}
        ref={inputRef}
        rows={rows}
        onChange={handleChange}
        maxLength={maxLength}
        {...testMetadata}
      />
      {charCount && (
        <Typography
          variant="T10"
          className="sb-textarea__characters"
          testMetadata={{ 'data-testid': 'textAreaCharCount' }}
        >{`${chars} ${maxLength ? ' / ' + maxLength : ''}`}</Typography>
      )}
    </div>
  );
};

export default TextArea;
