import classNames from 'classnames';
import { Control, Controller, RegisterOptions, Path } from 'react-hook-form';

// Components
import FormField, { FormFieldProps } from '../FormField';
import TextArea, { TextAreaProps } from '../TextArea';
import FormReadOnlyFieldTextValue from '../FormReadOnlyFieldTextValue';

// Types
import { TestMetadata } from 'types';

// Styles
import './FormTextAreaField.scss';

export interface FormTextAreaFieldProps<I extends Record<string, unknown>> {
  /** Name for field (Unique in the form) */
  fieldName: Path<I>;
  /** Display name for field */
  labelText?: string;
  /** Tooltip icon and hover text */
  tooltip?: string[];
  /** Mark field required (*) */
  required?: boolean;
  /** Disable field (greyed out) */
  disabled?: boolean;
  /** Display name for field */
  helper?: string;
  /** Class name */
  className?: string;
  /** Min length of characters required */
  minLength?: number;
  /** Max length of characters required */
  maxLength?: number;
  /** Additional textarea props for TextArea, based on the TextArea Component */
  textAreaProps?: Partial<TextAreaProps>;
  /** Sets test id */
  testMetadata?: {
    wrapper?: TestMetadata;
    textarea?: TestMetadata;
  };
  /** Default value from form */
  defaultValue?: string;
  /** Width of the Text Area */
  width?: 'small' | 'medium' | 'large' | 'auto';
  /** Rules to pass down to control */
  rules?: RegisterOptions;
}

const FormTextAreaField = <I extends Record<string, unknown>>({
  fieldName,
  labelText,
  tooltip,
  required,
  testMetadata,
  defaultValue,
  rules,
  helper,
  maxLength,
  minLength,
  textAreaProps,
  readOnly,
  truncate,
  ...otherProps
}: FormTextAreaFieldProps<I> & Partial<FormFieldProps<I>>): JSX.Element => {
  return (
    <FormField<I>
      labelText={labelText}
      tooltip={tooltip}
      fieldName={fieldName}
      required={required}
      readOnly={readOnly}
      helper={helper}
      {...testMetadata?.wrapper}
      control={(formMethods, props) => (
        <Controller
          name={fieldName as string}
          defaultValue={defaultValue !== undefined ? defaultValue : null}
          control={formMethods.control as Control<Record<string, unknown>>}
          rules={{
            required: required ? `${labelText} is required.` : undefined,
            minLength: minLength || undefined,
            maxLength: maxLength || undefined,
            ...rules,
          }}
          render={({ field: { onChange, onBlur, value, name } }) => {
            if (readOnly) {
              return (
                <FormReadOnlyFieldTextValue
                  value={value as string}
                  testMetadata={testMetadata?.textarea}
                  truncate={truncate}
                />
              );
            }

            return (
              <TextArea
                {...props}
                name={name as string}
                width="auto"
                {...testMetadata?.textarea}
                {...textAreaProps}
                className={classNames(
                  'sb-form-textarea-field-control',
                  textAreaProps?.className,
                  props.className,
                )}
                onChange={onChange}
                onBlur={onBlur}
                value={value as string}
              />
            );
          }}
        />
      )}
      {...otherProps}
    />
  );
};

export default FormTextAreaField;
